export default{
	name:'matter',
	props:{
		detaildata:Object
	},
	data(){
		return{
			videoing:false,
			staticvideo:''
		}
	},
	watch:{
		detaildata(newvalue,oldvalue){
			if(newvalue.staticvideo != ''){
				this.videoing = true
				this.staticvideo = newvalue.staticvideo
			}
		}
	}
}
